@import url(https://fonts.googleapis.com/css2?family=Space+Mono&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    margin:0;
    padding:0;
    box-sizing: border-box;
    font-family: 'Space Mono', monospace;
}

html {
  font-size: 62.5%;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
} 
 

/* navbar style */

.main-nav {
  width: 100%;
  height: 10rem;
  display: grid;
  grid-template-columns: 10rem 1fr 2fr 1fr 10rem;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px,
    rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
}

.logo {
  display: grid;
  /* background-color: #3b5998; */
  grid-column: 2/3;
  justify-content: flex-start;
  align-items: center;
}

.logo img{
    max-width: 76.54px;
    max-height: 76.54px;
    font-size: 2.5rem;
    font-weight: 400;
    color: cyan;
}

.menu-link {
  grid-column: 3/4;
}

.menu-link ul {
  height: 10rem;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.social-media {
  grid-column: 4/5;
}

.social-media ul {
  height: 10rem;
  display: grid;
  grid-template-columns: 3fr repeat(4, 1fr);
  align-items: center;
  justify-content: flex-end;
}

.social-media ul li {
  text-align: right;
}

.social-media ul li:hover {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
  }

.social-media ul li:first-child {
  grid-column: 2/3;
}

/* ----------- menu parts ----------------- */

.menu-link ul li {
  font-size: 1.8rem;
}

.menu-link ul li:hover {
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
  }


.social-media ul li {
  font-size: 3rem;
}

.social-media .hamburger-menu {
  display: none;
}

.facebook {
  color: #3b5998;
}

.instagram {
  color: #c32aa3;
}

.youtube {
  color: #ff0000;
}

.linkedin{
    color:#0e76a8;
}

/* hover animation */

.hover-underline-animation {
    display: inline-block;
    position: relative;
    /* color: #0087ca; */
  }
  
  .hover-underline-animation:after {
    content: '';
    position: absolute;
    width: 100%;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    height: 2px;
    bottom: 0;
    left: 0;
    background-color: #9AD0EC;
    -webkit-transform-origin: bottom right;
            transform-origin: bottom right;
    transition: -webkit-transform 0.25s ease-out;
    transition: transform 0.25s ease-out;
    transition: transform 0.25s ease-out, -webkit-transform 0.25s ease-out;
  }
  
  .hover-underline-animation:hover:after {
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
    -webkit-transform-origin: bottom left;
            transform-origin: bottom left;
  }


/* responsive */

@media (max-width: 1080px) {
  .main-nav {
    height: 8rem;
    grid-template-columns: 2rem 3fr 3fr 1fr 2rem;
  }

  .logo,
  .menu-link ul,
  .social-media ul {
    height: 8rem;
  }
  .logo img{
    max-width: 56.54px;
    max-height: 56.54px;
}
}

/* responsive css style  */
@media (max-width: 998px) {
  .main-nav {
    height: 7rem;
    grid-template-columns: 2rem 2fr 3fr 2rem 2rem;
  }

  .menu-link {
    display: none;
  }

  .logo,
  .social-media ul {
    height: 7rem;
  }

  .logo img{
      max-height: auto;
      max-width: 2em;
  }

  .mobile-menu-link {
    grid-column: 2/4;
    position: relative;
    z-index: 99;
  }

  .mobile-menu-link {
    background-color: white;
    height: 20rem;
    display: grid;
    grid-column: 2/5;
    align-items: center;
    padding-left: 3rem;
    transition: all 2s linear;
    -webkit-transform-origin: top;
            transform-origin: top;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px,
      rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
  }

  .mobile-menu-link ul {
    height: 20rem;
    display: flex;
    justify-content: space-around;
    flex-direction: column;
    align-items: flex-start;
  }

  .mobile-menu-link ul li:first-child {
    transition-delay: 0.2s;
  }

  .social-media {
    grid-row: 1/2;
    grid-column: 3/5;
    justify-items: end;
    align-items: center;
    transition: all 2s linear;
  }

  .social-media .social-media-desktop {
    height: 0;
    display: none;
  }

  .social-media {
    height: 7rem;
    display: flex;
    justify-self: end;
    align-items: center;
  }

  .social-media .hamburger-menu {
    display: block;
    font-size: 2.5rem;
  }
}

@media (max-width: 798px) {
  .main-nav {
    height: 6rem;
    grid-template-columns: 1rem 2fr 1fr 1fr 1rem;
  }

  .logo,
  .social-media ul {
    height: 6rem;
  }

  .social-media {
    height: 6rem;
    display: flex;
    justify-self: end;
    align-items: center;
  }

  .social-media .hamburger-menu {
    display: block;
    font-size: 2.5rem;
  }
}

@media (max-width: 520px) {
  .main-nav {
    height: 6rem;
    grid-template-columns: 1rem 4fr 1fr 1fr 1rem;
  }

  .logo,
  .social-media ul {
    height: 6rem;
  }

  .logo h2 {
    font-size: 2rem;
  }

  .social-media {
    height: 6rem;
    display: flex;
    justify-self: end;
    align-items: center;
  }

  .social-media .hamburger-menu {
    display: block;
    font-size: 2.5rem;
  }

  /* hero section  */

  .hero-section h1 {
    font-size: 3.8rem;
  }
}
.footer-main{
    display: flex;
    color: white;
    background-color: black;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.footer-i{
    color: red;
}

.footer-left, .footer-right, .footer-center{
    width: 33.33vw;
    border: 1px solid black;
    padding: 35px;
    
}

.footer-hr{
    border: 0.3px solid #4c4c4c;
    margin: 15px 0px;
}

.lfooter-content, .rfooter-content{
    line-height: 1.7;
    padding-bottom: 17px;
    font-family: 'Montserrat', sans-serif;
    color: #a7a4a4;
    font-size: 14px;
    
}

.contact-footer{
    display: flex;
    padding: 10px 0px;
    align-items: center;
}

.contact-text-footer{
    padding-left: 20px ;
    font-family: 'Montserrat', sans-serif;
}


.footer-inputbox{
    height: 45px;
    display: flex;
    align-items: center;
}
.footer-input{
    height: 100%;
    width: 80%;
    outline: none;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    border: none;
    padding: 1rem 2rem;
}


.footer-input-btn{
    height: 100%;
    background-color: red;

    width: 20%;
    justify-content: center;
    align-items: center;
    display: flex;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    font-family: 'Montserrat', sans-serif;
}

.cfooter-content{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.cfooter-post{
    width: 100%;
    display: flex;
    align-items: center;
    padding: 0px 18px;
}

.cfooter-text{
    padding: 22px;
}

.cfooter-text-title,.cfooter-text-info{
    font-family: 'Montserrat', sans-serif;
    padding: 3px 0px;
}

.cfooter-text-info{
    color: #a7a4a4;
}

.footer-input-btn:hover{
    cursor: pointer;
}

.img-footer{
    height: 65px;
    width: 90px;
}

.cfooter-post:hover{
    cursor: pointer;
}

@media screen and (max-width: 1023px) {

    .cfooter-post{
        margin: 5px;
    }
    .cfooter-text, .cfooter-post{
        padding: 0px 6px 0px 10px;
    }
}

@media screen and (max-width: 850px) {
   
    .footer-main{
        flex-direction: column;
    }

    .footer-left, .footer-right, .footer-center{
        width: 100%;
    }
}
